<template>
  <transition name="train-show">
    <div class="hs-container">
      <c-header isBackNav :title="title"></c-header>
      <hs-section v-show="showCell">
        <div class="info-train-container" ref="cell" :style="{top:`${extendRoute.startTop-55}px`}">
          <div class="info-left-container" >
            <div class="top-container">
              <div class="dep-info">
                <p>{{$t(beTime(extendRoute.DepartureDateTime), 'date', ['MM-dd', 'MM dd'])}} {{getWeeks(beTime(extendRoute.DepartureDateTime))}}</p>
                <p>{{beTime(extendRoute.DepartureDateTime,'hh:mm')}}</p>
                <p><span v-if="language === 'CN'" :class="[{start:dept=== 1},{middle:dept=== 2},{end:dept=== 3}]">{{dept|filterPassType}}</span>{{extendRoute.DepartStationName}}</p>
              </div>
              <div class="middle-info"  @click.stop="$emit('delegateForTableTime',extendRoute)">
                <p>{{extendRoute.TrainCode}}</p>
                <div>
                  <p></p>
                  <span>{{$t('base.Schedule')}}</span>
                  <p></p>
                </div>
                <p>{{extendRoute.RunTime}}</p>
              </div>
              <div class="arr-info">
                <p>{{$t(beTime(extendRoute.ArrivalDateTime), 'date', ['MM-dd', 'MM dd'])}} {{getWeeks(beTime(extendRoute.ArrivalDateTime))}}</p>
                <p>{{beTime(extendRoute.ArrivalDateTime,'hh:mm')}}<span class="ArrivalDays" v-if="extendRoute.ArrivalDays > 0">+{{extendRoute.ArrivalDays}}</span></p>
                <p><span v-if="language === 'CN'" :class="[{start:arr=== 1},{middle:arr=== 2},{end:arr=== 3}]">{{arr|filterPassType}}</span>{{extendRoute.ArrivalStationName}}</p>
              </div>
            </div>
            <!-- <div class="bottom-contianer">
              <div v-for="(item, index) in showSeats" :key='index'>
                <p v-if="language === 'CN'">{{item.name}}: {{item.have}}</p>
                <template v-else>
                  <p>{{item.name}}</p>
                  <p>{{item.have}}</p>
                </template>
              </div>
            </div> -->
          </div>
          <div class="info-right-container">
            <p>￥{{extendRoute.LowPrice}}<span>{{$t('base.From')}}</span></p>
          </div>
        </div>
      </hs-section>
      <hs-section v-show="showSeat">
        <hs-cell1
          :showLogo="false"
          :showSeparator="false"
          v-for="(seat,index) in extendRoute.Seats" :key="index"
          @click.native.stop="seat.Count > 0 ? $emit('delegateBook', extendRoute, seat) : ''">
            <div class="book-container"  ref="seat">
              <p>{{seat.TypeName}}</p>
              <p>{{$t('base.Remaining', 'function', seat.Count)}}</p>
              <p>￥{{seat.Price}}</p>
              <div :class="{noclick:seat.Count === 0}">{{$t('base.Book')}}</div>
            </div>
        </hs-cell1>
      </hs-section>
    </div>
  </transition>
</template>

<script type='text/ecmascript-6'>
import Vue from 'vue'
import CHeader from 'components/c-header/c-header';
import HsScroll from 'base/hs-scroll/hs-scroll';
import HsSection from 'base/hs-tableview/hs-section/hs-section';
import HsCell1 from 'base/hs-tableview/hs-cell-1/hs-cell-1';
import { addClass } from 'common/js/dom';
export default {
  components: {
    CHeader,
    HsScroll,
    HsSection,
    HsCell1
  },
  props: {
    title: {
      type: String,
      default: ''
    },
    extendRoute: {
      type: Object,
      default: () => {}
    }
  },
  filters: {
    filterPassType(val) {
      switch (val) {
        case 1:
          return Vue.prototype.$t('base.S')
        case 2:
          return Vue.prototype.$t('base.B_3')
        case 3:
          return Vue.prototype.$t('base.E')
        default:
          return Vue.prototype.$t('base.Unknown')
      }
    }
  },
  computed: {
    // 出发始过终 0-过路 1-过路终站 2-始发过路 3-始发终到
    dept() {
      if (this.extendRoute.PassByType === 2 || this.extendRoute.PassByType === 3) {
        return 1
      } else if (this.extendRoute.PassByType === 0 || this.extendRoute.PassByType === 1) {
        return 2
      }
    },
    // 到达始过终 0-过路 1-过路终站 2-始发过路 3-始发终到
    arr() {
      if (this.extendRoute.PassByType === 0 || this.extendRoute.PassByType === 2) {
        return 2
      } else if (this.extendRoute.PassByType === 1 || this.extendRoute.PassByType === 3) {
        return 3
      }
    },
    showSeatTypeCout() {
      return this.extendRoute.ShowSeats ? this.extendRoute.ShowSeats.length : 0;
    },
    showSeats() {
      let seatArr = [
        { name: '', have: '' },
        { name: '', have: '' },
        { name: '', have: '' }
      ];
      seatArr.forEach((item, index) => {
        if (this.showSeatTypeCout - 1 >= index) {
          item.name = this.extendRoute.ShowSeats[index].TypeName;
          item.have = this.extendRoute.ShowSeats[index].Count > 0 ? this.$t('base.Have') : this.$t('base.Havent');
        }
      });
      return seatArr;
    }
  },
  data() {
    return {
      showCell: false,
      showSeat: false
    };
  },
  mounted() {
    this.$nextTick(() => {
      if (!this.showCell || !this.showSeat) {
        this.showCell = true;
        setTimeout(() => {
          addClass(this.$refs.cell, 'showCell');
        }, 500);
        setTimeout(() => {
          this.showSeat = true;
          this.$refs.seat && this.$refs.seat.forEach((element, index) => {
            setTimeout(() => {
              addClass(element, 'showSeat');
            }, index * 200);
          });
        }, 900);
      }
    });
  },
  methods: {}
};
</script>

<style lang='stylus' scoped='' type='sylesheet/stylus'>
@import '~common/stylus/variable'
@import '~common/stylus/base'
.hs-container
  @extend .flex-column
  @extend .flex-start
  width 100%
  height 100%
  position absolute
  top 0
  left 0
  background-color $color_white
  z-index 10
  .header-title
    >h2
      font-size 1.1rem
      font-weight bold
  .showCell
    opacity 1 !important
    top 0 !important
  .info-train-container
    @extend .flex-between
    padding 15px 0
    margin 0px 10px
    position relative
    opacity 0.5
    transition all 500ms
    .info-left-container
      @extend .flex-column
      font-size 0.8rem
      width calc(100% - 75px)
      .top-container
        @extend .flex-between
        width 100%
        .dep-info
          @extend .flex-column
          @extend .flex-center
          width 33.33%
          P
            &:nth-child(1)
              font-size 0.7rem
              line-height 1.4em
              color $color_black_text_0
            &:nth-child(2)
              font-size 1.2rem
              font-weight bold
              line-height 1.2em
            &:nth-child(3)
              font-size 0.8rem
              line-height 1.4em
        .middle-info
          @extend .flex-column
          @extend .flex-center
          width 33.33%
          font-size 0.6rem
          >p
            &:first-child
              font-weight bold
              font-size 0.8rem
            &:last-child
              color $color_gray_text
          >div
            margin 3px 0
            @extend .flex-around
            >p
              width 12px
              height 1px
              background-color $color_gray_text
            >span
              border 1px solid $color_gray_layer
              padding 3px 5px
              border-radius 15px
              color $color_gray_text
        .arr-info
          @extend .flex-column
          @extend .flex-center
          width 33.33%
          P
            &:nth-child(1)
              font-size 0.7rem
              line-height 1.4em
              color $color_black_text_0
            &:nth-child(2)
              font-size 1.2rem
              font-weight bold
              line-height 1.2em
              position relative
            &:nth-child(3)
              font-size 0.8rem
              line-height 1.4em
          .ArrivalDays
            color $color_theme
            font-size 0.8rem
            position absolute
            top -4px
            right -16px
      .bottom-contianer
        @extend .flex-row
        @extend .flex-between
        width 100%
        margin-top 10px
        font-size 0.7rem
        color $color_gray_text
        line-height 1.2em
        >div
          @extend .flex-column
          @extend .flex-center
          width 33.33%
    .info-right-container
      width 75px
      text-align right
      color $color_theme
      >p
        font-size 1rem
        font-weight bold
        color $color_theme
        vertical-align middle
        >span
          font-size 0.8rem
          color $color_black_text_0
          margin-left 2px
  .book-container
    width 100%
    opacity 0
    top 20px
    position relative
    transition all 900ms
    @extend .flex-between
    padding 10px
    font-size 0.8rem
    background-color $color_white
    border-bottom 1px solid $color_gray_layer !important
    >p
      flex 1
      &:nth-child(2)
        color $color_gray_text
      &:nth-child(3)
        color $color_theme
    >div
      @extend .flex-center
      width 48px
      font-size 0.9rem
      background-color $color_theme
      color $color_white
      padding 8px 0
      border-radius 3px
    .noclick
      background-color $color_gray_background_0
  .showSeat
    top 0px !important
    opacity 1 !important
  .start
   background-color #ee7e7e
  .middle
   background-color #81d151
  .end
   background-color #72d3df
.train-show-enter-active, .train-show-leave-active
  transition all 400ms ease-in-out
.train-show-enter
  opacity 0
.train-show-leave-active
  transform translateX(100%)
</style>



